import Vue from "vue";
import Vuex from "vuex";
import router from "../router";
import axios from "axios";

Vue.use(Vuex);
Vue.use(router);

export default new Vuex.Store({
  state: {
    welcomeMsg: '',
    isHome: true,
    loading: true,
    countries: [],
    errors: {
      status: false,
      msg: ''
    }
  },
  mutations: {
    updatedCountries(state, payload) {
      state.countries = payload;
      state.errors.status = false;
    },
    showErrors(state, e) {
      state.errors.status = true;
      state.errors.msg = e;
    },
    updateIsHome(state, n) {
      state.isHome = n;
    }
  },
  actions: {
    getAllCountries(state) {
      axios.get('http://wakeb.tech/covidapi/public/api/cases')
      .then(res => {
        // console.log(res.data);
        state.commit('updatedCountries', res.data.data.countries_stat);
      }).catch(e => {
        console.log(e);
        state.commit('showErrors', e);
      });
    },
    checkCurrentPage(state, newValue) {
      state.commit('updateIsHome', newValue)
    }
  }
});
