<script>
  import CategoryCard from '../components/CategoryCard';
  
  import categoriesData from '../modules/categories';

  console.log(categoriesData['categories']);
  

  export default {
    name: 'Initiative',
    components: {
      CategoryCard
    },
    data () {
      return {
        categories: true,
        currentCat: null,
        category: 'other',
        categoriesData: categoriesData['categories']
      }
    },
    methods: {
      openCategory(cat) {
        this.$store.state.loading = true;
        this.categories = false;
        this.currentCat = cat;
        this.category = categoriesData['categories'][cat];

      },
      backToCategories() {
        this.categories = true;
      }
    },
    created() {
      this.$store.state.isHome = false;
      this.$store.state.welcomeMsg = this.$i18n.t('footer.initiativeTitle');

      this.$store.state.loading = false;

    },
    updated() {
      this.$store.state.welcomeMsg = this.$i18n.t('footer.initiativeTitle');
    }
  }
</script>

