import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Twitter from "../views/Twitter.vue";
import Map from "../views/Map.vue";
import Chatbot from "../views/Chatbot.vue";
import Country from "../views/Country.vue";
import Global from "../views/Global.vue";
import Arab from "../views/Arab.vue";
import WorldStat from "../views/WorldStat.vue";
import ArabStats from "../views/ArabStats.vue";
import NotFound from "../views/NotFound";
import Initiative from "../views/Initiative";


Vue.use(VueRouter);

const routes = [
  {path: "/", name: "home", component: Home},
  {path: "/twitter", name: "twitter", component: Twitter},
  {
    path: "/map", 
    name: "map", 
    component: Map,
    props: true,
    children: [
      {path: "", name: "Map", component: Global, props: true},
      {path: "arab-countries", name: "arab-countries", component: Arab},
      {path: "world-stat", name: "world-stat", component: WorldStat},
      {path: "arab-stats", name: "arab-stat", component: ArabStats}
      
    ]
  },
  {path: "/chatbot", name: "chatbot", component: Chatbot},
  {path: "/initiative", name: "initiative", component: Initiative},
  {path: "/country/:name", name: "country", component: Country},
  {path: "/404", component: NotFound},
  {path: "*", redirect: "/404"}
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
