<template>
  <div class="arab-top-recoverd text-center pt-5">
    <span class="text-center stat-title">{{ $t('arabStat.topRecoverd') }}</span>
    <div class="chart" ref="chartStatTwo"></div>
  </div>
</template>

<script>
   import axios from "axios";

  import * as am4core from "@amcharts/amcharts4/core";
  import * as am4charts from "@amcharts/amcharts4/charts";
  import am4themes_animated from "@amcharts/amcharts4/themes/animated";

  am4core.useTheme(am4themes_animated);

  export default {
    name: '',
    data () {
      return {
        topRecoverd: []
      }
    },
    methods: {
      getTopRecoverd() {
        axios.get('http://wakeb.tech/covidapi/public/api/arabs/top_recovered')
        .then(res => {
          // console.log(res.data);
          let countriesNames = Object.keys(res.data.data);
          this.topRecoverd = []
          for(let i = 0; i < countriesNames.length; i++) {

              let statObj = {
                country: countriesNames[i],
                visits: res.data.data[countriesNames[i]]
              }
              this.topRecoverd.push(statObj);
          }

          let chart2 = am4core.create(this.$refs.chartStatTwo, am4charts.XYChart);

          // chart.data = this.stats;
          chart2.data = this.topRecoverd;
          

          chart2.padding(40, 40, 40, 40);

          let categoryAxis2 = chart2.xAxes.push(new am4charts.CategoryAxis());
          categoryAxis2.renderer.grid.template.location = 0;
          categoryAxis2.dataFields.category = "country";
          categoryAxis2.renderer.minGridDistance = 60;
          categoryAxis2.renderer.inversed = true;
          categoryAxis2.renderer.grid.template.disabled = true;

          let labelTemplate2 = categoryAxis2.renderer.labels.template;
          labelTemplate2.fill = am4core.color("#fff");
          labelTemplate2.dy = 2; // moves it a bit down;
          labelTemplate2.inside = false; // this is done to avoid settings which are not suitable when label is rotated
          // labelTemplate2.background.fill = am4core.color("#fff");

          let valueAxis2 = chart2.yAxes.push(new am4charts.ValueAxis());
          valueAxis2.min = 0;
          valueAxis2.extraMax = 0.1;
          // valueAxis.rangeChangeEasing = am4core.ease.linear;
          // valueAxis.rangeChangeDuration = 2500;

          let series2 = chart2.series.push(new am4charts.ColumnSeries());
          // series.fill = "#0DD78C"; 
          series2.dataFields.categoryX = "country";
          series2.dataFields.valueY = "visits";
          series2.tooltipText = "{valueY.value}";
          series2.heatRules.push({
            "target": series2.columns.template,
            "property": "fill",
            "min": am4core.color("#9CE1CA"),
            "max": am4core.color("#0EE394"),
            "dataField": "valueY"
          });

          series2.columns.template.strokeOpacity = 0;
          series2.columns.template.column.cornerRadiusTopRight = 5;
          series2.columns.template.column.cornerRadiusTopLeft = 5;
          //series.interpolationDuration = 1500;
          //series.interpolationEasing = am4core.ease.linear;
          let labelBullet2 = series2.bullets.push(new am4charts.LabelBullet());
          labelBullet2.label.verticalCenter = "bottom";
          labelBullet2.label.dy = -10;
          labelBullet2.label.fill = "#ffffff"
          labelBullet2.label.text = "{values.valueY.workingValue.formatNumber('#.')}";

          chart2.zoomOutButton.disabled = true;

          chart2.colors.list = [
            am4core.color("#0dd7c8"),
            am4core.color("#0dd7ac"),
            am4core.color("#0DD78C"),
            am4core.color("#0DD77C"),
            am4core.color("#0DD76C")
          ];

          categoryAxis2.sortBySeries = series2;
          
          this.chart = chart2;

        }).catch((e) => {
          console.log(e);
        });
      }
    },
    mounted() {
      this.getTopRecoverd();
    },
    updated() {
      this.$store.state.welcomeMsg = this.$i18n.t('footer.mapTitle');
    },
    beforeDestroy() {
      if (this.chart) {
        this.chart.dispose();
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../style/arab-stats.scss';
</style>