<template>
  <div class="chatbot">
    <div class="container">
      <div class="robot_vector">
        <span class="logo">Mujib</span>
        <img src="../assets/robot.svg" width="70%" alt="corona">
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Chatbot',
    created() {
      this.$store.state.isHome = false;
      this.$store.state.welcomeMsg = this.$i18n.t('footer.chatbotTitle');

      this.$store.state.loading = false;

       this.getChatbot();
      
    },
    methods: {
      getChatbot() {
        let chatbotScript = document.createElement('script');
        chatbotScript.src = 'http://chatbotapp-project.azurewebsites.net/cdn/box.js';
        $('body').append(chatbotScript);
        // console.log('done');
        setTimeout(function (){ $('.chatbox__open--box').addClass('animated')}, 1500);
      },
      removeChatbot(){
        $('script[src*="http://chatbotapp"], script[src*="https://unpkg.com/simplebar"]').remove();

        $('link[href*="http://chatbotapp-project.azurewebsites.net/new_box"], link[href*="https://unpkg.com/simplebar@latest"]').remove();

        $('#chatboxContainer, .chatbox__open--box').remove();
        
      }
    },
    updated() {
      this.$store.state.welcomeMsg = this.$i18n.t('footer.chatbotTitle');
    },
    beforeDestroy() {
      this.$store.state.loading = true;
      this.removeChatbot();
    }
  }
</script>

<style lang="scss">
@import '../style/chatbot.scss';
.chatbot {
  height: 100%;
  width: 100%;
  .robot_vector {
    position: fixed;
    bottom: -110px;
    right: 0;
    .logo {
      position: absolute;
      display: inline-block;
      font-size: 3rem;
      text-transform: uppercase;
      right: 77%;
      bottom: 32%;
    }
    @media screen and (max-width: 700px) {
      right: -150px;
      bottom: -100px;
      .logo {right: 80%;}
      img {width: 55% !important;}
    }
    @media screen and (max-width: 450px) {
      right: -270px;
      .logo {font-size: 2rem;bottom: 34%;}
    }
  }
}
</style>