<template>
  <div class="arab-top-infected text-center pt-5">
    <span class="text-center stat-title">{{ $t('arabStat.topInfected') }}</span>
    <div class="chart" ref="chartStatOne"></div>
  </div>
</template>

<script>
  import axios from "axios";

  import * as am4core from "@amcharts/amcharts4/core";
  import * as am4charts from "@amcharts/amcharts4/charts";
  import am4themes_animated from "@amcharts/amcharts4/themes/animated";

  am4core.useTheme(am4themes_animated);

  export default {
    name: '',
    data () {
      return {
        topInfected: []
      }
    },
    methods: {
      getTopCases() {
        axios.get('http://wakeb.tech/covidapi/public/api/arabs/top_cases')
        .then(res => {
          // console.log(res.data.data);
          let countriesNames = Object.keys(res.data.data);
          this.topInfected = []
          for(let i = 0; i < countriesNames.length; i++) {

              let statObj = {
                country: countriesNames[i],
                visits: res.data.data[countriesNames[i]]
              }
              this.topInfected.push(statObj);
          }
    
          // this.stats = statistics;

          let chart = am4core.create(this.$refs.chartStatOne, am4charts.XYChart);

          // chart.data = this.stats;
          chart.data = this.topInfected;
          

          chart.padding(40, 40, 40, 40);

          let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
          categoryAxis.renderer.grid.template.location = 0;
          categoryAxis.dataFields.category = "country";
          categoryAxis.renderer.minGridDistance = 60;
          categoryAxis.renderer.inversed = true;
          categoryAxis.renderer.grid.template.disabled = true;

          let labelTemplate = categoryAxis.renderer.labels.template;
          labelTemplate.fill = am4core.color("#fff");
          labelTemplate.dy = 2; // moves it a bit down;
          labelTemplate.inside = false; // this is done to avoid settings which are not suitable when label is rotated
          // labelTemplate.background.fill = am4core.color("#000");

          let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
          valueAxis.min = 0;
          valueAxis.extraMax = 0.1;
          // valueAxis.rangeChangeEasing = am4core.ease.linear;
          // valueAxis.rangeChangeDuration = 2500;

          let series = chart.series.push(new am4charts.ColumnSeries());
          series.fill = "#0DD78C"; 
          series.dataFields.categoryX = "country";
          series.dataFields.valueY = "visits";
          series.tooltipText = "{valueY.value}";
          series.heatRules.push({
            "target": series.columns.template,
            "property": "fill",
            "min": am4core.color("#E69423"),
            "max": am4core.color("#D22626"),
            "dataField": "valueY"
          });

          series.columns.template.strokeOpacity = 0;
          series.columns.template.column.cornerRadiusTopRight = 5;
          series.columns.template.column.cornerRadiusTopLeft = 5;
          //series.interpolationDuration = 1500;
          //series.interpolationEasing = am4core.ease.linear;
          let labelBullet = series.bullets.push(new am4charts.LabelBullet());
          labelBullet.label.verticalCenter = "bottom";
          labelBullet.label.dy = -10;
          labelBullet.label.fill = "#ffffff"
          labelBullet.label.text = "{values.valueY.workingValue.formatNumber('#.')}";

          chart.zoomOutButton.disabled = true;

          chart.colors.list = [
            am4core.color("#0dd7c8"),
            am4core.color("#0dd7ac"),
            am4core.color("#0DD78C"),
            am4core.color("#0DD77C"),
            am4core.color("#0DD76C")
          ];

          categoryAxis.sortBySeries = series;
          
          this.chart = chart;

        }).catch((e) => {
          console.log(e);
        });
      }
    },
    mounted() {
      this.getTopCases()
    },
    updated() {
      this.$store.state.welcomeMsg = this.$i18n.t('footer.mapTitle');
    },
    beforeDestroy() {
      if (this.chart) {
        this.chart.dispose();
      }
    }
  }
</script>

