<template>
  <div class="search">
    <div class="search__box" :class="{animated:isOpened}">
      <div class="form-group">
        <input type="text" 
        v-model="searching" 
        @keyup="serchAboutCountry" 
        class="form-control" 
        autofocus
        :placeholder="placeholder">
      </div>
      <ul class="search__output list-unstyled" v-if="found || errors.status" >
        <li v-if="errors.status" class="text-danger">{{ errors.msg }}</li>
        <li v-for="country in searched" 
        :key="country.country_name" 
        @click.enter="getCountryDetails(country.country_name)"
        >{{country.country_name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {mapActions} from 'vuex';
  import {mapState} from 'vuex';
  // import simplebar from 'simplebar-vue';

  export default {
    name: 'Search',
    // components: {simplebar},
    props: ['isOpened'],
    data () {
      return {
        placeholder: this.$i18n.t('search.placeholder'),
        searching: '',
        found: false,
        searched: []
      }
    },
    computed: {
      ...mapActions(['getAllCountries']),
      ...mapState(['countries', 'errors'])
    },
    mounted() {
      this.getAllCountries
    },
    methods: {
      serchAboutCountry() {

        let filtered = [];
        // Update Filtered Countries to Component Data
        this.searched = filtered;
        // Search About a Country
        this.countries.forEach(element => {
          
          let check = element.country_name.toLowerCase().indexOf(this.searching.toLowerCase());
            
          if(check >= 0 && this.searching.trim()) filtered.push(element);
          
        });
        // Toggle Filtered Container According to the Existed Values in Filterd Array
        filtered.length > 0 ? this.found = true : this.found = false;
      },
      getCountryDetails(country) {
        console.log(country);
        
        if(country) {
          // Emit the Value to the Parent to Close Search Box
          this.$emit('closeSerchBox', false);
          // Go to the Selected Country
          this.$router.replace(`/map?country=${country}`);
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../style/search-box.scss';
</style>