<template>
  <div class="home">
    <div class="text-center">
      <router-link class="see__more--btn" to="/map">
        {{ $t('home.seeMore') }}
        <svg class="more" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
            <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                <polygon points="0 0 24 0 24 24 0 24"/>
                <path d="M12.2928955,6.70710318 C11.9023712,6.31657888 11.9023712,5.68341391 12.2928955,5.29288961 C12.6834198,4.90236532 13.3165848,4.90236532 13.7071091,5.29288961 L19.7071091,11.2928896 C20.085688,11.6714686 20.0989336,12.281055 19.7371564,12.675721 L14.2371564,18.675721 C13.863964,19.08284 13.2313966,19.1103429 12.8242777,18.7371505 C12.4171587,18.3639581 12.3896557,17.7313908 12.7628481,17.3242718 L17.6158645,12.0300721 L12.2928955,6.70710318 Z" fill="#ffffff" fill-rule="nonzero"/>
                <path d="M3.70710678,15.7071068 C3.31658249,16.0976311 2.68341751,16.0976311 2.29289322,15.7071068 C1.90236893,15.3165825 1.90236893,14.6834175 2.29289322,14.2928932 L8.29289322,8.29289322 C8.67147216,7.91431428 9.28105859,7.90106866 9.67572463,8.26284586 L15.6757246,13.7628459 C16.0828436,14.1360383 16.1103465,14.7686056 15.7371541,15.1757246 C15.3639617,15.5828436 14.7313944,15.6103465 14.3242754,15.2371541 L9.03007575,10.3841378 L3.70710678,15.7071068 Z" fill="#ffffff" fill-rule="nonzero" opacity="0.3" transform="translate(9.000003, 11.999999) rotate(-270.000000) translate(-9.000003, -11.999999) "/>
            </g>
        </svg>
      </router-link>
      <div class="chart" ref="chartdiv"></div>
    </div>
  </div>
</template>

<script>
  import * as am4core from "@amcharts/amcharts4/core";
  import * as am4maps from "@amcharts/amcharts4/maps";
  import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
  import am4themes_animated from "@amcharts/amcharts4/themes/animated";
  am4core.useTheme(am4themes_animated);

  import axios from 'axios';

  export default {
    name: "home",
    data() {
      return {
        loading: true,
        mapData: []
      }
    },
    methods: {
      getData() {
        axios.get('http://wakeb.tech/covidapi/public/api/cases').then(res => {

          this.mapData = [];

          // console.log(res.data.data.countries_stat);

          res.data.data.countries_stat.forEach(country => {
            
            // console.log(country);
            let countryData = {
              id: country.id,
              cases: country.cases,
              recoverd: country.total_recovered
            }

            this.mapData.push(countryData);
            
          });

          let chart = am4core.create(this.$refs.chartdiv, am4maps.MapChart);

          // Set map definition
          chart.geodata = am4geodata_worldLow;
          // For Touch Devices 
          chart.tapToActivate = true;
          // Set projection
          chart.projection = new am4maps.projections.Orthographic();
          chart.panBehavior = "rotateLongLat";
          chart.deltaLatitude = -20;
          chart.padding(20,20,20,20);

          
          // limits vertical rotation
          chart.adapter.add("deltaLatitude", (delatLatitude) => am4core.math.fitToRange(delatLatitude, -90, 90))

          // Create map polygon series
          let polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());

          // Make map load polygon (like country names) data from GeoJSON
          polygonSeries.useGeodata = true;

          // Bind Data to The Map
          polygonSeries.data = this.mapData;

          // Configure series
          let polygonTemplate = polygonSeries.mapPolygons.template;
          // polygonTemplate.tooltipText = "{name}";
          polygonTemplate.tooltipText = "{name}\nCases: {cases} \nRecoverd: {recoverd}";
          polygonTemplate.fill = am4core.color("#47c78a");
          polygonTemplate.stroke = am4core.color("#454a58");
          polygonTemplate.strokeWidth = 0.5;


          let graticuleSeries = chart.series.push(new am4maps.GraticuleSeries());
          graticuleSeries.mapLines.template.line.stroke = am4core.color("#ffffff");
          graticuleSeries.mapLines.template.line.strokeOpacity = 0.08;
          graticuleSeries.fitExtent = false;

          // graticuleSeries.mapLines.template.events.on("hit", function(ev) {
          //   console.log("clicked on ", ev.target);
          // }, this);

          chart.backgroundSeries.mapPolygons.template.polygon.fillOpacity = 0.1;
          chart.backgroundSeries.mapPolygons.template.polygon.fill = am4core.color("#454a58");

          // Create hover state and set alternative fill color
          let hs = polygonTemplate.states.create("hover");
          hs.properties.fill = chart.colors.getIndex(0).brighten(-0.5);

          let animation;
          setTimeout(function(){
            animation = chart.animate({property:"deltaLongitude", to:100000}, 20000000);
          }, 2000);

          chart.seriesContainer.events.on("down", function () {
            animation ? animation.stop() : '';
          });

          chart.seriesContainer.events.on("up", function () {
            setTimeout(function(){
              animation = chart.animate({property:"deltaLongitude", to:100000}, 20000000);
            }, 2000);
          });

            // polygonTemplate.events.on("hit", function(ev) {
            //   ev.target.series.chart.zoomToMapObject(ev.target);
            //   console.log(ev.target.dataItem.dataContext.name);
              
              
            // })

          this.chart = chart;
          this.loading = false;

          this.$store.state.loading = false;

        }).catch(e => {
          console.log(e);
          this.$store.state.loading = false
        })
      }
    },
    created() {
      this.$store.state.isHome = true;
      this.$store.state.welcomeMsg = this.$i18n.t('footer.homeTitle');
      this.getData();
    },
    updated() {
      this.$store.state.welcomeMsg = this.$i18n.t('footer.homeTitle');
      this.getData();
    },
    beforeDestroy() {
      if (this.chart) {
        this.chart.dispose();
      }
      this.$store.state.loading = true;
    }
};
</script>
<style lang="scss" scoped>
  @import '../style/home.scss';
</style>
