<script>
  import Search from './Search';
  // import Share from './Share';
  import {mapState} from 'vuex';

  export default {
    name: "navbar",
    components: {Search},
    data () {
      return {
        isOpened: false,
        isCollapsed: true,
        currentLang: 'en'
      }
    },
    created() {
      this.currentLang = this.$i18n.locale;
    },
    computed: {
      ...mapState(['isHome'])
    },
    methods: {
      changeLang(locale) {
        this.$store.state.loading = true;
        setTimeout(() => {

          this.$i18n.locale = locale;

          this.currentLang = locale;

          document.querySelector('body').classList.toggle('rtl');
          if(locale === 'en') {
            document.querySelector('head title').textContent = 'Wakeb COVID-19 Tracking Platform';
          } else {
            document.querySelector('head title').textContent = 'منصة واكب لتتبع فيروس كوفيد19';
          }
          

          this.$store.state.loading = false;
          
        }, 1000);
      },
      cancelSearchBox(payload) {
        this.isOpened = payload;
      }
    }
  };
</script>

