<template>
  <div class="category-card">
    <span class="return" @click="backwordNow()">
      <i class="fas fa-backward fa-lg"></i>
    </span>
    <div class="card shadow" style="width: 18rem;" v-for="cat in category" :key="cat.catHeading">
      <img :src="cat.catImg" @click="popupImage(cat.catImg)" class="card-img-top" :alt="cat.catHeading">
      <div class="card-body">
        <h5 class="card-title">{{ cat.catHeading }}</h5>
        <p class="card-text">{{ cat.catDesc }}</p>
        <a :href="cat.catLink" class="btn btn-link text-white" target="_blank">
          <i class="fas fa-link"></i>
        </a>
      </div>
    </div>
    <transition name="fade" mode="out-in">
      <div class="popup" v-if="popup.status" @click.stop="popup.status = false">
        <span class="close" @click="popup.status = false">x</span>
        <img :src="popup.src" alt="image" @click.stop="popup.status = true">
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'CategoryCard',
    props: ['category'],
    data() {
      return {
        popup: {
          status: false,
          src: null
        }
      }
    },
    created() {
      setTimeout(() => this.$store.state.loading = false, 1500);
    },
    methods: {
      popupImage(src) {
        this.popup.src = src;
        this.popup.status = true;
      },
      backwordNow() {
        this.$emit('backToCategories');
      }
    }
  }
</script>

<style lang="scss" scoped>
body.rtl {
  .category-card {
    .card {text-align: right;}
  }
}
  .category-card {
    position: relative;
    padding-top: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    .return {
      position: absolute;
      top: 5px;
      left: 50%;
      transform: translateX(-50%);
      z-index: 9;
      cursor: pointer;
      transition: 0.4s;
      border: 1px dashed #fff;
      padding: 6px 7.5px;
      border-radius: 50%;
      display: inline-block;
      background: rgb(13,215,140);
      background: linear-gradient(bottom, rgba(13,215,140,0.13068977591036413) 0%, rgba(46,46,58,0) 100%);
      &:hover {
       border-style: solid;
      }
    }
    .card {
      background: rgb(13,215,140);
      background: linear-gradient(bottom, rgba(13,215,140,0.13068977591036413) 0%, rgba(46,46,58,0) 100%);
      transition: 0.4s;
      margin: 10px;
      text-align: right;
      &:hover {
        transform: translateY(-5px);
      }
    }
    .card-text {color: #ddd;}
    .card-img-top {
      height: 150px;
      object-fit: cover;
      object-position: top;
      cursor: pointer;
    }
    .popup {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: 999;
      &:hover {cursor: pointer}
      .close {
        position: absolute;
        top: 60px;
        right: 30px;
        background-color: #fff;
        color: #000;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        z-index: 99;
        @media screen and (max-width: 480px){
          top: 15px;
          right: 15px;
          background-color: #000;
          color: #fff;
        }
      }
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 700px;
        max-height: 100%;
        &:hover {cursor: default}
        @media screen and (max-width: 767px) {
          max-width: 100%;
        }
      }
    }
  }
</style>