                  <path data-name="Path 34637" d="M290.2,484.516q-5.559,0-11.119,0a.7.7,0,0,1-.742-.876l.071-.437c.141-.844.274-1.689.425-2.532a6.272,6.272,0,0,1,.585-1.846,3.081,3.081,0,0,1,1.566-1.425l5.362-2.3c.33-.143.363-.189.363-.553,0-.453.007-.907-.006-1.361a.492.492,0,0,0-.12-.305,3.864,3.864,0,0,1-1.023-2.046c-.066-.315-.084-.638-.143-.955a.478.478,0,0,0-.154-.288,2.166,2.166,0,0,1-.9-1.979,2.242,2.242,0,0,1,.6-1.551c.127-.134.094-.279.029-.439a10.573,10.573,0,0,1-.434-1.193,1.3,1.3,0,0,1,.529-1.471,1.485,1.485,0,0,1,.826-.225.5.5,0,0,0,.462-.246,3.682,3.682,0,0,1,1.968-1.393,7.484,7.484,0,0,1,3.293-.336,6.152,6.152,0,0,1,2.305.676,2.943,2.943,0,0,1,1.53,1.773,3.46,3.46,0,0,1-.136,2.445.487.487,0,0,0,.062.556,2.123,2.123,0,0,1,.385,1.313,2.9,2.9,0,0,1-.364,1.507,2.311,2.311,0,0,1-.354.447.557.557,0,0,0-.165.375,5.555,5.555,0,0,1-.614,2.386,4.229,4.229,0,0,1-.438.61.516.516,0,0,0-.132.354c0,.47.01.941,0,1.411a.434.434,0,0,0,.305.462c1.2.508,2.393,1.023,3.589,1.535.586.252,1.169.509,1.758.752a3.222,3.222,0,0,1,1.874,1.981,11.4,11.4,0,0,1,.482,2.034c.13.763.248,1.528.376,2.291a.705.705,0,0,1-.669.844c-.049,0-.1,0-.148,0Z" transform="translate(-278.329 -460.712)" fill="#b4b4b4"/>
                </svg>
                <span class="side__card--number mx-1 mx-xl-2 mb-2">{{ countryData.total_cases_per_1m_population }}</span>
                <span class="side__card--desc mx-1 mx-xl-2 mb-2">{{ $t('global.person') }}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios";
  
  import * as am4core from "@amcharts/amcharts4/core";
  import * as am4maps from "@amcharts/amcharts4/maps";
  import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
  import am4themes_animated from "@amcharts/amcharts4/themes/animated";

  am4core.useTheme(am4themes_animated);
  

  export default {
    name: 'Global',
    data() {
      return {
        currCountry: 'China',
        countryData: {},
        toggleCountry: false,
        countriesNames: [],
        allCountries: []
      }
    },
    computed: {
      passedCountry() {
        return this.$route.query.country;
      }
    },
    methods: {
      getCountries () {
        axios.get('http://wakeb.tech/covidapi/public/api/cases')
        .then(res => {
          // console.log(res.data.data.countries_stat);
          this.countriesNames = [];
          this.allCountries = [];

          res.data.data.countries_stat.filter((con) => {

            this.countriesNames.push(con.country_name);

            // push Data
            // if(con.region === '') con.region = '_';
            let flagSrc = '';
            if(typeof con.id == 'string') flagSrc = con.id.toLowerCase();

            // console.log(flagSrc);
            
            con.flag = `https://cdn.jsdelivr.net/gh/hjnilsson/country-flags@latest/svg/${flagSrc}.svg`;

            this.allCountries.push(con);

            this.allCountries.filter(ele => {

              if(ele.country_name === this.currCountry) {

                this.countryData = ele;

                this.toggleCountry = true;

                setTimeout(() => this.toggleCountry = false, 500);
              }
            });
          });

          if(this.$store.state.loading) {

            let chart = am4core.create(this.$refs.mapAMChart, am4maps.MapChart);

            // Set map definition
            chart.geodata = am4geodata_worldLow;

            // Set projection
            chart.projection = new am4maps.projections.Miller();

            // Create map polygon series
            let polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());
            polygonSeries.exclude = ["AQ"];

            // polygonSeries.fill = am4core.color("#0DD78C")

            // Make map load polygon (like country names) data from GeoJSON
            polygonSeries.useGeodata = true;

            polygonSeries.data = this.allCountries;

            // Configure series
            let polygonTemplate = polygonSeries.mapPolygons.template;
            polygonTemplate.tooltipText = "{name}\n Cases: {cases}";
            // \nCases: {cases}
            // Map Colors 
            polygonTemplate.fill = am4core.color("#bdc3c7");
            polygonTemplate.stroke = am4core.color("#454a58");

            // Create hover state and set alternative fill color
            let hs = polygonTemplate.states.create("hover");
            hs.properties.fill = am4core.color("#47c78a");

            polygonTemplate.events.on("hit", (ev) => {
            // ev.target.series.chart.zoomToMapObject(ev.target);
            console.log(ev.target.dataItem.dataContext);
            
            
            if(ev.target.dataItem.dataContext.name === 'Palestinian Territories') {
              this.currCountry = 'Palestine';
              this.filterData();
            }
            else if(ev.target.dataItem.dataContext.name === 'United Arab Emirates') {
              this.currCountry = 'UAE';
              this.filterData();
            }
            else if(ev.target.dataItem.dataContext.name === 'United States') {
              this.currCountry = 'USA';
              this.filterData();
            }
            else if(!this.countriesNames.includes(ev.target.dataItem.dataContext.name)) {
              this.currCountry = ev.target.dataItem.dataContext.name;
              this.countryData = {
                deaths: 0,
                new_deaths: 0,
                serious_critical: 0,
                total_recovered: 0,
                cases: 0,
                new_cases: 0,
                active_cases: 0,
                total_cases_per_1m_population: 0
              }
            } 
            else {
              this.currCountry = ev.target.dataItem.dataContext.name;
              this.filterData();
            }

          })

          let changeCountry = '';
          if(this.countryData) {
            let indexCountry = 1;
            changeCountry = setInterval(() => {
              this.currCountry = this.countriesNames[indexCountry]
              this.filterData();
              indexCountry >= this.countriesNames.length -1 ? indexCountry = 0 : indexCountry ++;
            }, 15000);
          }

            // Stop Changing the Current Country
            if(this.$route.query.country) {
              clearInterval(changeCountry);
              console.log(this.$route.query.country);
              
            }

            this.chart = chart;

            this.$store.state.loading = false;

          }

        }).catch(e => {
          console.log(e);
          this.$store.state.loading = false;
        });
      },
      filterData() {

        this.allCountries.filter(ele => {

          if(ele.country_name === this.currCountry) {

            this.countryData = ele;

            this.toggleCountry = true;
  
            setTimeout(() => this.toggleCountry = false, 500);
          }
        });
      },
      getSpacificCountry() {
        if(this.$route.query.country) {
          // console.log(this.passedCountry);
          this.currCountry = this.passedCountry;
        }
      }
    },
    updated() {
      this.$store.state.welcomeMsg = this.$i18n.t('footer.mapTitle');
      
    },
    created () {
      // Get All Data 
      this.getCountries();
      // IF the data Came From The Query
      this.getSpacificCountry();
    },
    beforeDestroy() {
      if (this.chart) {
        this.chart.dispose();
      }
      this.$store.state.loading = true;
    }
  }
</script>
<style lang="scss" scoped>
  @import '../style/global-countries.scss';

</style>