<template>
  <div class="country">
    <div v-if="!loading && country">
      <div class="text-center">Country Name: <span>{{country.country_name}}</span></div>
      <div class="stat mt-4">
        <span>Cases: {{country.cases}}</span>
        <span>Deaths: {{country.deaths}}</span>
        <span>Total Recovered: {{country.total_recovered}}</span>
        <span>New Deaths: {{country.new_deaths}}</span>
        <span>New Cases: {{country.new_cases}}</span>
        <span>Serious Critical: {{country.serious_critical}}</span>
        <span>Active Cases: {{country.active_cases}}</span>
        <span>Total Cases Per 1M Population: {{country.total_cases_per_1m_population}}</span>
      </div>
    </div>
    <div v-if="loading" class="text-center">
      <div class="spinner-border text-primary" role="status">
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    name: 'Country',
    data () {
      return {
        country: {},
        loading: false
      }
    },
    created() {
      this.$store.state.isHome = false;
      this.$store.state.welcomeMsg = 'Wakeb Coronavirus Tracking platform';
    },
    mounted() {
      this.getCurrCountry();
    },
    updated() {
      this.getCurrCountry()
    },
    methods: {
      getCurrCountry() {
        axios.get(`http://localhost:3000/allcountries?country_name=${this.$route.params.name}`)
        .then(res => {
          this.country = res.data[0]
          // console.log(this.country);
          // this.loading = false;
          
        }).catch(e => console.log(e))
      }
    }
  }
</script>
<style lang="scss" scoped>
.spinner-border {margin-top: 30vh;}
.text-center span {
  display: inline-block;
  border-radius: 10px;
  padding: 2px 5px;
  background-color: #fff;
  color: green;
  font-size: 1.1rem;
  font-weight: 600;
}

.stat {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  span {
    display: inline-block;
    padding: 5px;
    margin: 5px;
    border: 1px solid #ccc;
    border-radius: 10px;
  }
}
</style>